
#wrap-box > div {
  width: 100%;
  height: 100%;
}
#wrap-box .wrap-title {
  margin: 0.4rem 0;
  font-size: 0.9rem;
  text-align: center;
  vertical-align: middle;
}
#wrap-box .wrap-title img {
  width: 2%;
}
#wrap-box .wrap-ctn ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  list-style: none;
}
#wrap-box .commodity-state {
  position: relative;
  width: 48%;
  border: 1px solid #d0d0d0;
  box-sizing: border-box;
  margin-bottom: 0.4rem;
}
#wrap-box .commodity-box {
  position: relative;
  width: 89.67%;
  margin: 0.5rem 0.5rem 0 0.5rem;
}
#wrap-box .commodity-img:before {
  content: "";
