}
#wrap-box .commodity-img img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  vertical-align: top;
}
#wrap-box .commodity-description {
  display: block;
  margin: 0.538rem 0;
  color: #333;
  font-size: 0.75rem;
  text-align: left;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
#wrap-box .commodity-postage {
  font-style: normal;
  color: #E72B58;
}
#wrap-box .commodity-offprice {
  left: 0;
  bottom: 0;
