}
#wrap-box .commodity-price {
  margin-left: 2.25%;
  color: #999;
  font-size: 0.7rem;
  text-decoration: line-through;
  vertical-align: middle;
}
#wrap-box .cart-icon {
  width: 1.5rem;
  display: inline-block;
  right: 0;
  bottom: 0;
  vertical-align: middle;
}
#wrap-box .cart-icon img {
  width: 100%;
}



