
#addaddress {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #333;
  background: #f4f4f4;
}
#addaddress .line {
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
#addaddress ul {
  padding-left: 0.5rem;
  background: #fff;
}
#addaddress ul > li ::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999;
}
#addaddress ul > li :-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
#addaddress ul > li ::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
#addaddress ul > li :-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #999;
}
#addaddress ul > li > .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8rem;
}
#addaddress ul > li > .content > .left {
  width: 4rem;
  line-height: 2.5rem;
}
#addaddress ul > li > .content > .right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#addaddress ul > li > .content > .right > input {
  line-height: 2.5rem;
  border: none;
  font-size: 0.8rem;
  text-indent: 10px;
}
#addaddress ul > li > .content > .right > input:focus {
  outline: none;
}
#addaddress ul > li > .content > .right .el-select {
  width: 100%;
}
#addaddress ul > li > .content > .right .el-select .el-input__inner {
  height: 2.5rem;
  font-size: 0.8rem;
  border: none;
}
#addaddress #wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-top: 0.5rem;
}
#addaddress .save {
  padding: 0.5rem;
  background: #f4f4f4;
}
#addaddress .mint-button--danger {
  font-size: 0.8rem;
  height: 2.5rem;
}



