
@import '../../static/css/animate.css';
#home {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#home #home_wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  overflow: hidden;
  position: relative;
}
#home #home_wrap > div {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
#home .pre_next-enter-active {
  -webkit-animation: slideInRight 0.5s;
          animation: slideInRight 0.5s;
}
#home .pre_next-leave-active {
  -webkit-animation: slideOutLeft 0.5s;
          animation: slideOutLeft 0.5s;
}
#home .pre_next-enter-active.right {
  -webkit-animation: slideInLeft 0.5s;
          animation: slideInLeft 0.5s;
}
#home .pre_next-leave-active.right {
  -webkit-animation: slideOutRight 0.5s;
          animation: slideOutRight 0.5s;
}
#footbar {
  height: 2.7rem;
  width: 100%;
  font-size: 0;
  border-top: 1px solid #cccccc;
}
#footbar > div {
  width: 33.33%;
  height: 100%;
  font-size: 0.65rem;
  display: inline-block;
  text-align: center;
  padding-top: 0.2rem;
  box-sizing: border-box;
  color: #666666;
}
#footbar > div .iconfont {
  font-size: 1.2rem;
}
#footbar .router-link-exact-active {
  color: #ff4672;
}
#footbar > div.addcart {
  background: #e72755;
  color: white;
  line-height: 2.7rem;
  font-size: 0.8rem;
  padding-top: 0;
  vertical-align: top;
}



