
#approved {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #333;
}
#approved .line {
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
#approved #wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #f4f4f4;
}
#approved #wrap > .warning {
  width: 50%;
  background-color: #efefef;
  line-height: 5rem;
  padding: 0 2rem;
  border-radius: 0.5rem;
  border: 1px solid #ddd;
  margin: 2rem auto;
  opacity: 0;
  transition: all .5s ease;
}
#approved #wrap > .warning.out {
  opacity: 1;
}
#approved .item {
  background: white;
}
#approved .item > .content_wrap {
  padding: 0 0.5rem;
}
#approved .item > .content_wrap > .isapproved > .list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
