}
#approved .item > .content_wrap > .isapproved > .list > .left {
  width: 4.5rem;
  line-height: 2.5rem;
  text-indent: 0.4rem;
}
#approved .item > .content_wrap > .isapproved > .list > .right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#approved .item > .content_wrap > .isapproved > .list > .right > input {
  line-height: 2.5rem;
  font-size: 0.8rem;
  border: none;
}
#approved .item > .content_wrap > .isapproved > .list > .right > input:focus {
  outline: none;
}
#approved .item > .content_wrap > .isapproved > .hasapproved {
  display: block;
  text-align: center;
  line-height: 2.5rem;
  font-size: 0.9rem;
}
#approved .item > .content_wrap > .information {
  padding: 0.7rem 0 0.5rem;
}
