
#addressedit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  color: #333;
}
#addressedit .line {
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
#addressedit #wrap {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-top: 0.5rem;
  background: #f4f4f4;
}
#addressedit #wrap > ul {
  height: 100%;
  overflow: scroll;
}
#addressedit #wrap > ul > li {
  background: white;
  margin-bottom: 0.5rem;
  color: #666;
}
#addressedit #wrap > ul > li > .content {
  padding-left: 0.5rem;
}
#addressedit #wrap > ul > li > .content > .top {
  padding: 0.5rem 0.5rem 0.4rem 0;
}
#addressedit #wrap > ul > li > .content > .top > .name_phone {
  font-size: 0.9rem;
  color: #333;
  line-height: 1.2rem;
}
#addressedit #wrap > ul > li > .content > .top > .name_phone > span {
  float: right;
}
#addressedit #wrap > ul > li > .content > .top > .address {
  font-size: 0.8rem;
  line-height: 1.7rem;
}
#addressedit #wrap > ul > li > .content > .bottom {
  height: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8rem;
}
#addressedit #wrap > ul > li > .content > .bottom > .left {
  line-height: 2rem;
}
#addressedit #wrap > ul > li > .content > .bottom > .left > .select-wrap {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 1px solid #ddd;
  border-radius: 50%;
  transition: all 0.2s;
  vertical-align: middle;
}
#addressedit #wrap > ul > li > .content > .bottom > .left > .select-wrap.active {
  border-color: #e72755;
  background-color: #e72755;
}
#addressedit #wrap > ul > li > .content > .bottom > .left > .select-wrap > .iconfont {
  opacity: 0;
  font-size: 1rem;
  color: #fff;
  transition: all 0.2s;
  position: relative;
  top: -0.4rem;
}
#addressedit #wrap > ul > li > .content > .bottom > .left > .select-wrap > .iconfont.active {
  opacity: 1;
}
#addressedit #wrap > ul > li > .content > .bottom > .right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: right;
  padding-right: 0.5rem;
  line-height: 2rem;
}
#addressedit .add {
  padding: 0.5rem;
  background: #f4f4f4;
}
#addressedit .mint-button--danger {
  font-size: 0.8rem;
  height: 2.5rem;
}
#addressedit .mint-button--default {
  height: 2.5rem;
  background: #fff;
}



