
#commodity {
  background-color: #F4F4F4;
  overflow: hidden;
}
#commodity > #wrap {
  height: 100%;
  overflow: scroll;
  color: #333;
}
#commodity > #wrap .swiper-container,
#commodity > #wrap .swiper-wrapper {
  height: 12rem;
}
#commodity > #wrap .swiper-pagination-bullet-active {
  background: #ff9292;
}
#commodity > #wrap .swiper-slide {
  height: 100%;
}
#commodity > #wrap .swiper-slide > img {
  width: 100%;
  height: 100%;
}
#commodity > #wrap .swiper-container {
  background-color: pink;
  margin-bottom: 0.55rem;
}
#commodity > #wrap .commodity-img {
  width: 100%;
  height: 14rem;
  margin-bottom: 0.55rem;
}
#commodity > #wrap .commodity-img img {
  width: 100%;
  height: 100%;
}
#commodity > #wrap .commodity ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  margin-bottom: 0.5664rem;
  list-style: none;
}
#commodity > #wrap .commodity ul li {
  width: 100%;
}
#commodity > #wrap .commodity-header {
  position: relative;
  height: 4.02139844rem;
  background-color: #FFF;
  border-bottom: 1px solid #F4F4F4;
}
#commodity > #wrap .commodity-name {
  position: absolute;
  top: 0.5rem;
  left: 0.6rem;
  font-size: 0.8rem;
}
#commodity > #wrap .commodity-sale {
  position: absolute;
  left: 0.59375rem;
  bottom: 0.338rem;
  color: #E72B58;
  font-size: 1.4rem;
  font-weight: 600;
  vertical-align: middle;
}
#commodity > #wrap .commodity-sale .originalprice {
  color: #999;
  font-weight: normal;
  text-decoration: line-through;
}
#commodity > #wrap .commodity-sale sub {
  font-size: 0.9rem;
  vertical-align: bottom;
}
#commodity > #wrap .commodity-posted {
  border: 1px solid #E72B58;
  padding: 0 0.2rem;
  margin-left: 0.5rem;
  font-style: normal;
  font-size: 0.5rem;
  font-weight: normal;
}
#commodity > #wrap .commodity-support {
  height: 2.5rem;
  width: 100%;
  background-color: #FFF;
  border-bottom: 1px solid #F4F4F4;
  position: relative;
}
#commodity > #wrap .commodity-support > .el-icon-arrow-right {
  color: #E72B58;
  width: 10%;
  position: absolute;
  right: 0;
  top: 0;
}
#commodity > #wrap .commodity-support > .el-icon-arrow-right:before {
  line-height: 2.5rem;
  font-size: 0.8rem;
}
#commodity > #wrap .commodity-support ul {
  height: 100%;
  width: 90%;
  white-space: nowrap;
  overflow: scroll;
}
#commodity > #wrap .commodity-support ul li {
  display: inline-block;
  width: 32%;
  height: 2.5rem;
  line-height: 2.5rem;
  font-size: 0.75rem;
  text-align: center;
}
#commodity > #wrap .commodity-support ul li > .iconfont {
  color: #e72755;
  vertical-align: middle;
}
#commodity > #wrap .commodity-details {
  background-color: #FFF;
}
#commodity > #wrap .commodity-details > .content {
  font-size: 0.75rem;
  text-indent: 2em;
  padding: 0 0.6rem;
  color: #666;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
#commodity > #wrap .commodity-details > .content > .title {
  text-indent: 0;
  color: #333;
  padding-top: 0.4rem;
}
#commodity > #wrap .commodity-details > .more .el-icon-arrow-down {
  text-align: center;
  color: #555;
}
#commodity > #wrap .commodity-details span {
  display: -webkit-box;
  margin: 0.5664rem 0.625rem 0;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  overflow: hidden;
}
#commodity > #wrap .commodity-service {
  height: 2.5rem;
  background-color: #FFF;
}
#commodity > #wrap .commodity-service span {
  display: block;
  height: 2.5rem;
  margin-left: 0.6rem;
  line-height: 2.8rem;
  font-size: 0.75rem;
}



