
#indexpage {
  overflow: hidden;
}
#indexpage > #wrap {
  height: 100%;
  overflow: hidden;
}
#indexpage > #wrap .swiper-container,
#indexpage > #wrap .swiper-wrapper {
  height: 8.5rem;
}
#indexpage > #wrap .swiper-pagination-bullet-active {
  background: #ff9292;
}
#indexpage > #wrap .swiper-slide {
  height: 100%;
}
#indexpage > #wrap .swiper-slide > div {
  width: 100%;
  height: 100%;
}
#indexpage > #wrap .swiper-slide > div > img {
  width: 100%;
  height: 100%;
}
#indexpage > #wrap .swiper-container {
  background-color: pink;
  margin-bottom: 0.55rem;
}
#indexpage > #wrap .store-state {
  height: 4rem;
  background: url('../assets/storebg.png') no-repeat 0 0;
  background-size: 100% 100%;
  margin-bottom: 0.6rem;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#indexpage > #wrap .store-img {
  bottom: 0;
  width: 4.5rem;
  height: 4rem;
  box-sizing: border-box;
  padding: 0.25rem 0.5rem;
  background: url('https://g-search3.alicdn.com/img/bao/uploaded/i4/i4/94941669/TB2fquliCtkpuFjy0FhXXXQzFXa_!!94941669.jpg_230x230.jpg_.webp') no-repeat 0 0;
  background-size: 100% 100%;
  background-origin: content-box;
}
#indexpage > #wrap .store-name {
  padding-top: 0.8rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#indexpage > #wrap .store-name > p {
  font-size: 0.8rem;
  line-height: 1.2rem;
}



