
#personcenter {
  color: #333;
  overflow: hidden;
}
#personcenter .line {
  height: 1px;
  background: #ccc;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
#personcenter #wrap {
  height: 100%;
  background: #f4f4f4;
}
#personcenter .head {
  height: 2.5rem;
  padding: 1rem;
