  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 0.5rem;
}
#personcenter .head > .icon {
  height: 2.5rem;
  width: 2.5rem;
}
#personcenter .head > .icon > img {
  width: 100%;
  height: 100%;
